@import '../../../styles/Variables.scss';
@import '../../../styles/Mixins.scss';

.shop-info-contianer {
  @include mobileDashboardScreen;
  @include shadow3;
  position: relative;

  .shop-title {
    @include title;
    font-size: $one;
  }

  // taps wrapper
  .tabs-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .active {
      position: relative;
      @include subTitle;
      @include button4;
      color: $tomato;
      cursor: pointer;

      &::after {
        content: '';
        position: absolute;
        left: 0;
        bottom: -0.2rem;
        width: 100%;
        height: 2px;
        background-color: $red;
      }
    }

    .passive {
      @include subTitle;
      color: $black;
      cursor: pointer;
      @include button4;
      font-size: $one;
    }
  }

  // Shop Products
  .shop-products {
    @include productsGrid;
  }

  // Shop Events
  .shop-events-wrapper {
    .shop-events {
      @include mobileProducts;
    }

    .no-events {
      @include subTitle;
      font-size: $one;
    }
  }

  // Shop reviews
  .shop-reviews-wrapper {
    @include productsGrid;

    .shop-review {
      flex: 1;
      @include shadow3;
      display: flex;
      flex-direction: row;
      gap: 1rem;

      .image-container {
        .image {
          width: 5rem;
          height: 5rem;
          object-fit: cover;
          border-radius: 50%;
        }
      }

      .user-rating {
        .reviewer-name {
        }

        .comment {
          font-size: $one;
        }

        .review-date {
          font-size: $one;
          color: $blue;
        }
      }
    }

    .no-review {
    }
  }
}

