@import '../../../styles/Variables.scss';
@import '../../../styles/Mixins.scss';

.cart-info-wrapper {
  flex: 1;
  @include shadow3;

  .cart-info-title {
    @include subTitle;
    font-size: $aafs;
  }

  .info-container {
    display: flex;
    flex-direction: row;
    align-items: center;

    .subTitle {
      @include subTitle;
    }

    .outcome {
      @include paragraph;
    }
  }

  .total-price {
    @include subTitle;
  }

  // Form
  .cart-info-form {
    .input-container {
      position: relative;
      @include inputContainer;

      .input-field {
        @include inputField;
        margin-bottom: $radius;
        padding-left: 2rem;

        &::placeholder {
          color: gray;
        }
      }

      .input-label {
        position: absolute;
        top: 0;
        left: 2rem;
        color: rgba(204, 204, 204, 0);
      }

      .input-highlight {
        position: absolute;
        bottom: 0.2rem;
        left: 0;
        height: 2px;
        width: 0;
        background-color: $yellow;
        transition: all 0.3s ease;
      }

      .input-field:focus + .input-label {
        top: -5px;
        font-size: 12px;
        color: #007bff;
      }

      .input-field:focus + .input-label + .input-highlight {
        width: 100%;
      }

      .icon {
        position: absolute;
        top: 1.1rem;
        left: 0.5rem;
        font-size: 1rem;
        color: $green;
      }

      // displaying password
      .password-display {
        position: absolute;
        top: 50%;
        right: 1rem;
        transform: translatey(-50%);
        font-size: 1.3rem;
        cursor: pointer;
      }
    }

    .submit-btn {
      @include googleButton;
    }
  }
}
