@import '../../../styles/Variables.scss';
@import '../../../styles/Mixins.scss';

.shop-settings-wrapper {
  @include mobileDashboardScreen;

  .title {
    @include title;
    font-size: $one;
  }

  .image-form-wrapper {
    width: $laptop;
    margin: auto;
    @include shadow3;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    .image-container {
      .image {
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        object-fit: cover;
      }
    }
    .image-form {
      .input-field {
        display: none;
      }

      .image-label {
        @include inputField;
        font-size: $one;
        cursor: pointer;
        .icon {
          color: $green;
        }
      }
    }
  }

  .form {
    width: $laptop;
    margin: auto;
    @include shadow3;

    // Inpute container
    .input-container {
      position: relative;
      @include inputContainer;

      .input-field {
        @include inputField;
        padding-left: 2rem;
        font-size: $one;
      }

      .input-label {
        position: absolute;
        top: 0;
        left: 2rem;
        color: rgba(204, 204, 204, 0);
      }

      .input-highlight {
        position: absolute;
        bottom: 0.5rem;
        left: 0;
        height: 2px;
        width: 0;
        background-color: $green;
        transition: all 0.3s ease;
      }

      .input-field:focus + .input-label {
        font-size: 12px;
        color: #81a3e6;
      }

      .input-field:focus + .input-label + .input-highlight {
        width: 100%;
      }

      .icon {
        position: absolute;
        top: 1rem;
        left: 0.5rem;
        font-size: 1rem;
        color: $green;
      }

      // displaying password
      .password-display {
        position: absolute;
        top: 50%;
        right: 1rem;
        transform: translatey(-50%);
        font-size: 1.3rem;
        cursor: pointer;
      }
    }

    // Send button
    .shop-settings-btn {
      @include button1;
      font-size: $one;
    }
  }
}

